@import "global.scss";

.page-reports {
  padding: 0 16px;

  .reports {
    left: $sidebar-width;
    position: relative;
    top: $header-height;
    width: calc(100% - #{$sidebar-width});

    h2 {
      color: #000;
      font: 500 20px/24px Roboto;
      margin: $space * 3 0;
    }

    h3 {
      font: 500 18px/22px Roboto;
      color: #000000;
      margin-bottom: $space;
    }

    .container {
      display: flex;

      .info {
        text-align: start;
        width: 25%;

        &__category {
          font: 500 14px/17px Roboto;
          color: #3e3e3e;
        }

        &__target {
          text-align: left;
          font: 500 10px/13px Roboto;
          letter-spacing: 0;
          color: #959393;
          opacity: 1;
          padding-bottom: $space * 2;
        }

        &__total {
          font: 700 14px/17px Roboto;
          color: #3e3e3e;
        }
      }

      .info-right {
        text-align: end;
        display: flex;
        flex-direction: column;

        .summary {
          flex-grow: 1;

          .info__category {
            font: 20px/24px Roboto;
          }
        }

        .total {
          font: 800 20px/24px Roboto;
          letter-spacing: 0;
          color: #3e3e3e;
          align-items: center;
          display: flex;
          flex-grow: 1;

          &__sublabel {
            font: 500 20px/24px Roboto;
            letter-spacing: 0;
            color: #728dac;
            padding-left: $space * 2;
          }
        }
      }
    }

    .chart {
      width: 25%;
      text-align: center;
    }

    .line {
      width: 100%;
      height: 1px;
      margin: 10px 0px;
      background: #005da5 0% 0% no-repeat padding-box;
    }
  }
}
