@import "global";

.userbox {
  border-top: 1px rgba(255, 255, 255, 0.2) solid;
  color: #005ca6;
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 38px auto 20px;
  grid-template-rows: 2fr;
  margin-left: auto;
  padding: 20px;

  img {
    border-radius: 50%;
    height: 36px;
    grid-column: 1;
    grid-row: 1 / 3;
    object-fit: none;
    overflow: hidden;
    width: 36px;
  }

  .username {
    font: 500 14px/17px Roboto;
    letter-spacing: 0;
  }

  .role {
    font: 500 12px/14px Roboto;
    grid-column: 2;
    grid-row: 2;
    letter-spacing: 0;
  }

  .logout {
    background-color: #005ca6;
    cursor: pointer;
    grid-column: 3;
    grid-row: 1 / 3;
    mask-image: url(../images/logout.svg);
    mask-repeat: no-repeat;
    -webkit-mask-image: url(../images/logout.svg);
    -webkit-mask-repeat: no-repeat;
  }
}
