@import "global.scss";

.page-totems {
  padding: 0 16px;

  .totems {
    color: #000;
    left: $sidebar-width;
    position: relative;
    top: $header-height;
    width: calc(100% - #{$sidebar-width});

    .headlines {
      display: flex;
      padding: 3px 0px;
      align-items: center;

      .name {
        width: 600px;
      }

      .switch {
        text-align: center;
        width: 104px;

        span {
          color: #728dac;
          font: 500 12px/14px Roboto;
          letter-spacing: 0;
        }
      }
    }

    .list {
      & > div:nth-child(odd) {
        background: $white;
      }
    }

    &__actions {
      display: flex;
      font: 16px/19px Roboto;
      height: $space * 6;

      .action {
        align-items: center;
        display: flex;

        &__label {
          margin-right: $space * 2;
        }

        &:first-child {
          margin-left: $space * 2;
        }

        &:nth-child(2) {
          margin-left: 100px;
        }
      }
    }

    h2 {
      font: 500 20px/24px Roboto;
    }
  }
}
