@import "global";
@import "icons";

$button-width: 134px;

.header {
  align-items: center;
  background: #f5f6f7;
  box-shadow: 0px 3px 6px #00000029;
  display: flex;
  height: $header-height;
  left: $sidebar-width;
  position: fixed;
  top: 0;
  width: calc(100% - #{$sidebar-width});
  z-index: 1;

  .nav {
    .btn {
      background: initial;
      border: initial;
      color: #005192;
      cursor: pointer;
      font: 12px/14px Roboto;
      font-weight: 600;
      height: $header-height;
      min-width: $button-width;
      opacity: 0.5;
      outline: none;
      position: relative;
      text-transform: uppercase;
      transition: all 0.3s ease;

      & + button {
        border-left: 1px solid #00000029;
      }

      &:after {
        background: #168eea;
        bottom: 0;
        content: "";
        display: block;
        height: 3px;
        left: 50%;
        position: absolute;
        transform: translateX(-50%);
        transition: all 0.3s ease;
        width: 0;
      }

      &.active,
      &:hover {
        opacity: 1;

        &:after {
          width: 100%;
        }
      }
    }
  }
}
