@import "global";

.white {
  background-color: #FFFFFF !important;
}

.totem {
  background-color: #eeeeee;
  display: flex;
  padding: 16px;
  height: 56px;
  align-items: center;

  .name {
    width: 600px;
    
    span {
      color: #000000;
      font: 500 16px/19px Roboto;
      letter-spacing: 0;
    }
  }

  .switch {
    text-align: center;
    width: 104px;
  }

  .notification {
    background-image: url(../images/notification-on.svg);
    background-repeat: no-repeat;
    background-position: center;
    height: 24px;
    display: block;

    &--off {
      background-image: url(../images/notification-off.svg);
    }
  }

  .badge {
    margin-top: -8px;
    margin-left: 3px;
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 6px;
    background-color: red;
    color: white;
    padding: 1px;
    font-size: 10px;
    font-weight: bold;

    &--off {
      display: none;
    }
  }
}
