@import "global.scss";

.page-login {
  background-image: url(../images/loginBackground.png);
  background-size: 100% auto;
  background-repeat: no-repeat;
  min-height: 100vh;
  background-position: center;

  .header,
  .sidebar {
    display: none;
  }

  .login {
    display: block;
    position: fixed;
    width: 417px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: #f5f6f7;

    .logo {
      background-color: #005192;
      background-image: url(../images/logoYPF.svg);
      background-repeat: no-repeat;
      background-position: center;
      height: 87px;
      display: block;
    }

    h1 {
      display: block;
      margin: 0;
      padding: 45px 50px 25px 110px;
      background-image: url(../images/online.svg);
      background-repeat: no-repeat;
      background-position: 50px 50px;
      text-align: left;
      font: 400 26px/32px Montserrat;
      letter-spacing: 0;
      color: #005ca6;
      border-bottom: 1px solid #e8e8e8;

      span {
        display: block;
        font: 500 12px/22px Roboto;
        letter-spacing: 0;
        color: #168eea;
      }
    }

    form {
      border-bottom: 1px solid #e8e8e8;
      margin: 0;

      fieldset {
        border: 0;
        padding: 0 50px;

        label {
          display: block;
          margin: 25px 0 0 0;
          font: 300 12px/14px Roboto;
          letter-spacing: 0;
          color: #000000;
          opacity: 0.6;
        }

        input {
          display: block;
          margin: 10px 0 0 0;
          padding: 0 15px;
          height: 48px;
          width: 100%;
          border: 0;
          background: #ffffff;
          box-shadow: 0px 3px 6px #00000005;
          border-radius: 6px;
          -webkit-box-sizing: border-box;
          -moz-box-sizing: border-box;
          box-sizing: border-box;  
        }

        a {
          display: block;
          margin: 10px 0 0 0;
          font: 300 12px/14px Roboto;
          letter-spacing: 0;
          color: #168eea;
        }

        .error {
          display: none;
        }

        &.hasError {
          input {
            border: 1px $danger solid;
          }

          .error {
            display: block;
            color: $danger;
            height: auto;
          }
        }
      }

      button {
        display: block;
        margin: 15px 50px;
        width: calc(100% - 100px);
        height: 48px;
        background: transparent linear-gradient(180deg, #005ea8 0%, #00357c 100%) 0% 0% no-repeat padding-box;
        box-shadow: 0px 3px 6px #00000005;
        border-radius: 6px;
        color: white;
        border: 0;
        cursor: pointer;
        text-transform: uppercase;
      }
    }

    .error {
      display: flex;
      justify-content: center;
      align-items: center;
      color: $danger;
      height: 70px;
    }
  }
}
