@import "global";

.sidebar {
  background-color: #005192;
  bottom: 0;
  left: 0;
  position: fixed;
  top: 0;
  width: $sidebar-width;
  z-index: 1;

  &__logo {
    background-color: #005192;
    background-image: url(../images/logoYPF.svg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 145px auto;
    box-shadow: 0px 3px 6px #00000029;
    display: block;
    height: $header-height;
  }

  &__nav {
    list-style: none;
    margin: 0;
    padding: 0;

    .item {
      a {
        align-items: center;
        border-bottom: 1px solid #005da5;
        color: $white;
        display: flex;
        font: 500 16px/19px Roboto;
        height: 56px;
        justify-content: flex-start;
        letter-spacing: 0;
        margin: 0;
        padding: 0 25px;
        text-decoration: none;
      }

      &__icon {
        transform: scale(1.5);
        width: $space * 2;
      }

      &__label {
        margin-left: $space * 4;
      }

      &--active {
        background: $white;

        .item {
          &__icon {
            color: #015092;
          }
          &__label {
            color: #015092;
          }
        }
      }

      .arrow {
        margin-left: auto;
        transform: scale(2);
      }

      .children {
        margin: 0;
        padding: 0;

        .item {
          a {
            padding-left: calc(25px + #{$space * 6});
          }

          &__label {
            margin: 0;
            opacity: 0.4;
          }

          .child--active {
            .item__label {
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
